<template>
  <div id="calculator" v-bind:class="{'scnd':(clicked === true)}">

  <div class="display">{{current||0}}</div>

  <div class="btn first" @click="clear">AC</div>
  <div class="btn first" @click="minus">+/–</div>
  <div class="btn first" @click="percent">%</div>
  <div @click="divide" class="btn first operator">÷</div>

  <div @click="append('7')" class="btn">7</div>
  <div @click="append('8')" class="btn">8</div>
  <div @click="append('9')" class="btn">9</div>
  <div @click="multiply" class="btn operator">×</div>

  <div @click="append('4')" class="btn">4</div>
  <div @click="append('5')" class="btn">5</div>
  <div @click="append('6')" class="btn">6</div>
  <div @click="subtract" class="btn operator">−</div>

  <div @click="append('1')" class="btn">1</div>
  <div @click="append('2')" class="btn">2</div>
  <div @click="append('3')" class="btn">3</div>
  <div @click="add" class="btn operator">+</div>

  <div @click="append('0')" class="btn">0</div>
  <div @click="dot" class="btn">.</div>
  <div class="btn operator" @click="del">⇤</div>
  <div @click="equal" class="btn operator">=</div>
  </div>
</template>

<script>
export default {
  data(){
    return {
      previous:false,
      clicked:false,
      operator:null,
      current:'0'
    }
  },
  methods:{
    clear(){
      this.current='';
    },
    minus(){
      this.current = this.current.toString(); if (this.current=='NaN') this.current='0';
      if (this.current === '' || this.current === '0') return;
      this.current = this.current.charAt(0) === '-' ? this.current.slice(1) : `-${this.current}`; 
    },
    percent(){
      this.current = `${Math.round((parseFloat(this.current)/100)*10000000000)/10000000000}`;

    },
    append(number){
    if (this.clicked)
    {
      this.clicked=false; this.current="";
    }
    this.current=`${this.current}${number}`;
    this.current=parseFloat(this.current);
    },
    dot(){
      this.current = this.current.toString();
    if (this.current.indexOf('.') === -1)
      this.current = this.current+'.';
    if (this.current==='.') this.current='0.';
    },
    del()
    {
      this.current = this.current.toString();
      this.current = this.current.slice(0,this.current.length-1);
    },
    binary(){
      this.clicked = true;
      this.previous = this.current;
    },
    add(){
      this.operator = (a, b) => a + b;
      this.binary();
    },
    subtract(){
      this.operator = (a, b) => a - b;
      this.binary();
    },
    divide(){
      this.operator = (a, b) => a / b;
      this.binary();
},
    multiply(){
      this.operator = (a, b) => a * b;
      this.binary();
    },
    equal(){
      if (this.previous !== false)
      this.current = `${this.operator(
        parseFloat(this.previous), 
        parseFloat(this.current))}`;
      this.previous = false;
    }
  }
}
</script>

<style>
body{
  background:#fff;
}
.btn{
  text-align:center;
  line-height:60px;
  border-radius:60px;
  margin:5px 5px;
  font-size:30px;
  background:#233;
}
.zero{
    grid-column: 1/3;
}
.first{
  background:#999090;
}
.operator{
  background:#f80;
}
.scnd .operator{
  background:#080;
}
#calculator {
  background:#000;
  color:#fff;
  margin:10px auto;
  padding: 10px;
  font-size:30px;
font-family:Avenir,Sans;
display:grid;
grid-template-columns: repeat(4, 1fr);
grid-auto-rows: minmax(50px, auto);
}
.display {
  grid-column: 1 / 5;
  font-size:70px;
  color:#fff;
  text-shadow: #000 1px 1px 0, #000 -1px -1px 0, #000 -1px 0 0;
  text-align:right;
  overflow:hidden;
  padding:0 10px;
  background: #000;
}
</style>



